* {
	font-family: "Roboto", sans-serif; }
header {
	position: relative;
	height: 50px; }
nav {
	position: fixed;
	width: 100%;
	height: 50px;
	background-color: #212121;
	z-index: 2; }
.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;
	.menu_link {
		font-size: 15px;
		font-weight: 300;
		color: #ffffff; }
	.menu_item {
		border-right: 2px dotted black;
		padding-right: 28px; } }
.subheader {
	position: absolute;
	top: 50px;
	width: 100%;
	height: 84px;
	background-color: rgba(0, 0, 0, .6);
	.subheader_logo {
		display: block;
		margin-top: 16px; }
	.subheader_official {
		display: block;
		margin-top: 6px;
		opacity: 0.5;
		font-size: 13px;
		font-weight: 100;
		color: #ffffff; }
	.subheader_call {
		display: block;
		margin-top: 19px;
		font-size: 12px;
		font-weight: 300;
		color: #ffffff;
		text-align: right; }
	.subheader_phone {
		display: block;
		margin-top: 8px;
		font-size: 22px;
		font-weight: 400;
		text-transform: uppercase;
		color: #ffffff;
		text-align: right;
		line-height: 12px; }
	.subheader_btn {
		display: block;
		margin-top: 19px;
		width: 178px;
		height: 52px;
		background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
		border: none;
		border-radius: 4px;
		box-shadow: inset 0px -3px 0px 0px rgba(0,0,0,0.12);
		font-size: 14px;
		font-weight: 400;
		text-transform: uppercase;
		text-shadow: 1px 1px 0px rgba(0,0,0,0.21);
		color: #ffffff; } }
.promo {
	min-height: 800px;
	background: url("../img/bg/uber_bg.jpg") center center/cover no-repeat;
	padding: 150px 0 135px 0;
	.promo_header {
		margin: 0;
		font-size: 60px;
		font-weight: 900;
		text-transform: uppercase;
		color: #ffffff;
		text-align: center;
		line-height: 60px; }
	.promo_subheader {
		margin: 0;
		margin-top: 22px;
		font-size: 36px;
		font-weight: 700;
		text-align: center;
		color: #ffffff;
		line-height: 36px; }
	.promo_descr {
		margin-top: 23px;
		font-size: 18px;
		line-height: 24px;
		font-weight: 400;
		color: #ffffff;
		text-align: center; }
	.promo_btn {
		display: block;
		margin: 115px auto 0 auto;
		width: 265px;
		height: 69px;
		background-image: linear-gradient(0.0deg, #1eacc7 0%, #4ce2ff 100%);
		border: none;
		border-radius: 4px;
		box-shadow: inset 0px -4px 0px 0px rgba(0,0,0,0.11);
		font-size: 18px;
		font-weight: 400;
		text-transform: uppercase;
		text-shadow: 1px 1px 0px rgba(0,0,0,0.2);
		color: #ffffff; } }
.causes {
	min-height: 898px;
	padding: 71px 0 65px 0;
	.label {
		display: block;
		margin: 0 auto;
		width: 112px;
		height: 24px;
		background-color: #000000;
		font-size: 11px;
		line-height: 25px;
		font-weight: 100;
		text-transform: uppercase;
		color: #ffffff;
		text-align: center; }
	.title {
		display: block;
		margin: 0 auto;
		margin-top: 33px;
		font-size: 38px;
		font-weight: 700;
		text-transform: uppercase;
		color: #222222;
		text-align: center; }
	.subtittle {
		font-size: 17px;
		line-height: 21px;
		font-weight: 400;
		color: #222222;
		text-align: center; }
	.causes_block {
		display: flex;
		margin-top: 40px;
		justify-content: center;
		align-items: center; }
	.causes_round {
		display: flex;
		margin-right: 26px;
		justify-content: center;
		align-items: center;
		min-width: 113px;
		min-height: 113px;
		border-radius: 100%;
		background-color: #1eacc7; }
	.causes_icons {
		width: 80px;
		height: 66px;
		color: #ffffff; }
	.causes_descr {
		width: 343px;
		.causes_title {
			font-size: 17px;
			line-height: 21px;
			font-weight: 400;
			color: #1eacc7;
			text-align: left; }
		.causes_text {
			color: #202020;
			font-size: 15px;
			font-style: normal;
			font-weight: 300;
			line-height: 21px;
			text-align: justify; } } }

